import axios from 'axios';

const apiUrl = path => {
  let ret = '';
  if (window.location.href.indexOf('localhost') > -1) {
    ret = `${window.location.protocol}//localhost:4000${path}`;
  }else{
    ret = `${path}`;
  }
  // console.log(ret);
  return ret;
};

export const loadVideos = async () => {
  try {
    const res = await axios.get(apiUrl('/api/videos'));
    return res.data.data;
  } catch (e) {
    return { error: e };
    // if (e.response) return e.response;
    // if (e.request) return e.request;
    // return e;
  }
};



